body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.title-input {
    height: 1.875rem;
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.float-edit {
    position: relative;
    flex: 0 0 5%;
    max-width: 5%;
    display: table-cell;
    vertical-align: middle;
    text-align: center;
    background: url(/static/media/edit.41b4b352.svg) no-repeat center center;
}

.float-edit:hover {
    cursor: pointer;
}

.float-form {
    display: flex;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
}

.float-input {
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    flex: 0 0 95%;
    max-width: 95%;
}

.float-input-ac {
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    flex: 0 0 95%;
    max-width: 95%;
    min-height: 77px;
}

.error-text {
    color: red;
    font-size: small;
}

.input-length {
    font-size: small;
    float: right;
    color: #808080;
}

.input-length-alert {
    font-size: small;
    float: right;
    color: red;
}

.buttons-container {
    display: flex;
}

.buttons-group {
    margin: auto;
}

.button-item {
    margin: 0 1em;
}
.board-item {
    cursor: pointer;
}

.task-count {
    float: right;
    color: #808080;
}

.board-id {
    float: left;
    color: #808080;
}
